import { Layout, Menu, Breadcrumb, Icon } from 'antd';
const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;
import {Switch,Route,Link,NavLink} from 'react-router-dom'
import {history,Lazyload} from '&'
import { Head } from '../../components/head';
import {observer} from 'mobx-react'
import  key from '../../mobx/key'

@observer
export default class Hone extends Component {
    state = {
        collapsed: false,
    };
    onCollapse = collapsed => {
        this.setState({ collapsed });
    };
    changeurl=(e)=>{
        key.key(e.item.props.eventKey)
        history.push(e.item.props.path)
    }
    render() {
        return (
            <div >
                <Header style={{boxSizing:'border-box'}}>
                    <Head></Head>
                </Header>
                <Layout style={{ minHeight: '100vh' }}>
                    <Sider collapsible collapsed={this.state.collapsed} onCollapse={this.onCollapse}>
                        <div className="logo" />
                        <Menu theme="dark" mode="inline" onClick={this.changeurl.bind(this)} selectedKeys={key.arr}>  
                            <Menu.Item key='1' path="/home/showscore?key=1" >
                                <Icon type="desktop" />
                                <span >班级成绩</span>
                            </Menu.Item>
                            <Menu.Item key='2' path="/home/score?key=2" >
                                <Icon type="pie-chart" />
                                <span >成绩对比</span>
                            </Menu.Item>
                            <SubMenu
                                key="sub1"
                                title={
                                    <span>
                                        <Icon type="team" />
                                        <span>项目</span>
                                    </span>
                                }
                            >
                                <Menu.Item key='3' path="/home/showpro?key=3"  >个人项目</Menu.Item>
                                <Menu.Item key='4' path="/home/insertscore?key=4"  >提交项目</Menu.Item>
                            </SubMenu>
                            <SubMenu
                                key="sub2"
                                title={
                                    <span>
                                        <Icon type="file" />
                                        <span>面试</span>
                                    </span>
                                }
                            >
                                <Menu.Item key='5' path="/home/question?key=5"  >面试宝典</Menu.Item>
                                <Menu.Item key='6' path="/home/answer?key=6"  >面试答案</Menu.Item>
                            </SubMenu>
                            <Menu.Item key='7' path="/home/mine?key=7" >
                                <Icon type="user" />
                                <span >个人信息</span>
                            </Menu.Item>
                        </Menu>
                    </Sider>
                    <Layout >
                       <Content style={{padding:50,boxSizing:'border-box'}}>
                            <Switch>
                                <Route path='/home/showscore' component={Lazyload(()=>import('../showscore'))}></Route>
                                <Route path='/home/insertscore' component={Lazyload(()=>import('../insertscore'))}></Route>
                                <Route path='/home/showpro' component={Lazyload(()=>import('../showpro'))}></Route>
                                <Route path='/home/question' component={Lazyload(()=>import('../question'))}></Route>
                                <Route path='/home/answer' component={Lazyload(()=>import('../answer'))}></Route>
                                <Route path='/home/score' component={Lazyload(()=>import('../score'))}></Route>
                                <Route path='/home/mine' component={Lazyload(()=>import('../mine'))}></Route>
                            </Switch>
                       </Content>
                    </Layout>
                </Layout>
            </div>
        );
    }
}

