
import './index.scss'
import { Avatar } from 'antd'
import { Menu, Dropdown, Icon } from 'antd'
import {history,axios} from '&'
import {connect} from 'react-redux'
import { getuser } from '../../actions'



const menu = (
    <Menu onClick={handleMenuClick}>
        <Menu.Item key="0" >
            <span>修改信息</span>
        </Menu.Item>
        <Menu.Divider />
        <Menu.Item key="1">
            <span>退出登录</span>
        </Menu.Item>
    </Menu>
);


function handleMenuClick(e){
    if(e.key==0){
        history.push('/home/mine')
    }
    if(e.key==1){
        sessionStorage.token=''
        history.push('/login')
    }   
}

@connect(
    (state)=>{
        return{
            user:state.getIn(['user','user'])
        }
    }
)
export class Head extends Component {
    click=()=>{
        this.inp.click()
    }
    upload=()=>{
        var file=this.inp.files[0]
        console.log(file)
        var data=new FormData()
        data.append('avatar',file)
        axios.post('/react/uploadavatar',data).then(res=>{
            this.props.dispatch(getuser({url:'/react/getuser'}))
        })
    }
    componentDidMount(){
        this.props.dispatch(getuser({url:'/react/getuser'}))
        setTimeout(()=>{
            if(!this.props.user.username){
                history.push('/home/mine')
            }
        },400)
        if(!sessionStorage.token){
            history.push('/login')
        }
    }
    render() {
        var {
            user
        }=this.props
        return (
            <div style={{ display: 'flex', justifyContent: "space-between" }}>
                <h2 className='title' style={{ fontSize: 25, color: 'aliceblue', fontWeight: 'bold' }}>成绩管理系统</h2>
                <div>
                    <Dropdown overlay={menu} trigger={['click']}>
                        <span className="ant-dropdown-link" disabled={true} style={{ marginRight: 40, color: 'white' }}>
                            {user&&user.username}
                        <Icon type="caret-down" theme="filled" style={{marginLeft:10}} />
                        </span>
                    </Dropdown>
                    <Avatar size={58}  icon="user" src={user&&user.avatar&&user.avatar.replace(/public/,"http://123.56.84.4:1900/")}  onClick={this.click}/>
                    <input type="file" ref={el=>this.inp=el} style={{display:'none'}} onChange={this.upload}/>
                </div>
            </div>
        )
    }
}